.restrictions-popup {
  max-width: 700px;
  box-shadow: none; }
  @media screen and (max-width: 576px) {
    .restrictions-popup {
      overflow: auto;
      padding: 0 15px; } }
  .restrictions-popup .modal-header {
    border-bottom: none; }
  .restrictions-popup .header {
    font-size: 24px;
    font-weight: bold;
    color: #354F53;
    text-align: center;
    margin-bottom: 31px;
    line-height: 1; }
    @media screen and (max-width: 576px) {
      .restrictions-popup .header {
        font-size: 18px; } }
    .restrictions-popup .header .sub {
      color: #00D7AF;
